body{
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #12151c; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #20222f 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #171922 
}

button{
  cursor: pointer;
}

button:disabled{
  cursor: no-drop;
}

*{
  outline: none;
}
.history-list {
  width: 100%;
  padding-top: 2px;
  overflow: auto;
  transition-duration: .3s;
}

.history-button{
  color: #ffffff;
  padding: 4px 8px;
  display: none;
}

.history-item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 12px;
  margin: 2px 2px 0px 2px;
}

.history-item.red {
  background-image: linear-gradient(to right, #23161f, #171721);
  color: red;
}

.history-item.green {
  background-image: linear-gradient(
    to right,
    #182120,
    #151821
  );
  color: green;
}

@media(max-width: 768px){
  .history-button{
    display: block;
  }

  .history-list{
    max-height: 0;
  }

  .list-expanded{
    max-height: 100vh;
  }
}
.outside-toggle {
  background-color: #131418;
  border-radius: 20px;
  height: 20px;
  width: 40px;
  display: inline-flex;
  transition-duration: .3s;
  cursor: pointer;
}

.inside-toggle {
  background-color: #9499af;
  height: 16px;
  width: 16px;
  border-radius: 100px;
  transition-duration: .3s;
  margin: 2px;
}

.checked-toggle .inside-toggle {
  background-color: #ffffff;
  transform: translateX(20px);
}

.checked-toggle {
  background-color: #45b21b;
}

.dis-toggle{
  cursor: no-drop;
}
.num-input-container {
  display: flex;
  align-items: center;
  margin-left: 5px;
}

.num-input {
  background-color: #000000;
  padding: 8px;
  margin-left: 5px;
  width: 100px;
}

.num-input input {
  width: 30px;
  background-color: transparent;
  color: #fedd14;
  border: none;
}

.num-input span {
  color: #fedd14;
}

.num-input button {
  background-color: transparent;
  color: #393c4d;
  border: none;
  font-size: 19px;
}

.num-input-label {
  color: #7b83ac;
}

.panel-container {
  background-color: #151821;
  margin: 4px;
  display: flex;
  padding: 8px;
}

.panel-part{
  display: inline-flex;
  background-color: #272935;
  padding: 4px;
  width: 215px;
  margin: 1px;
  justify-content: flex-end;
  align-items: center;
}

.panel-dash{
  display: flex;
  flex-direction: column;
}

.bet-btn{
  background-image: linear-gradient(to bottom, #fcdf55, #f5d42f);
  border: none;
  color: rgb(66, 66, 66);
  font-size: 21px;
  margin: 1px;
  padding: 20px 20px;
  width: 250px;
}

.bet{
  background-image: linear-gradient(to bottom, rgb(0, 194, 0), rgb(0, 116, 0));
  color: yellow;
}

.cancel{
  background-image: linear-gradient(to bottom, rgb(235, 0, 0), rgb(179, 0, 0));
  color: #ffffff;
}
.screen-container {
  height: 80vh;
  max-width: 100%;
  background-image: url(/apollo/static/media/bg.5d72d5ee.png);
  background-size: cover;
  margin: 4px;
  position: relative;
  background-position: 0 0;
  -webkit-animation-duration: 200000ms;
          animation-duration: 200000ms;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.screen-animated {
  -webkit-animation-name: fly;
          animation-name: fly;
}

.top-center {
  position: absolute;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 0;
}

.ship-container {
  position: absolute;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  bottom: 0;
  width: 200px;
  transition-duration: 1s;
}

.outside-loading {
  height: 4px;
  width: 100%;
  text-align: left;
}

.ship-container .spaceship-img {
  width: 100px;
}

.loading-rail {
  height: 4px;
  transition-duration: 1s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  background-color: yellow;
}

.ship-container h4 {
  text-align: center;
}

.off {
  transform: translateY(-100%);
}

.red {
  color: red;
}

.green {
  color: green;
}

@-webkit-keyframes fly {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 0 -10000%;
  }
}

@keyframes fly {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 0 -10000%;
  }
}

.sidebar-header h3{
    color: #ffffff;
    text-align: right;
    padding-right: 20px;
}

.main-container {
  display: grid;
  grid-template-columns: 6% 69% 25%;
  background-color: #0c0f16;
  min-height: 100vh;
}

@media(max-width: 768px){
  .main-container{
    grid-template-columns: 100%;
  }
}
